.section-about {
    background-color: $color-light-grey-1;
    padding: 25rem 0;
    margin-top: -20vh;
}

.section-features {
    padding: 20rem 0;
    background-image: linear-gradient(to right bottom, rgba($primary-color-light, 0.8), rgba($primary-color-dark, 0.8)),
        url("../../assets/nat-4.jpg");
    background-size: cover;
    transform: skewY(-7deg);
    margin-top: (-15vh);

    & > * {
        transform: skewY(7deg);
    }
}

.section-tours {
    background-color: $color-light-grey-1;
    padding: 25rem 0 20rem;
    margin-top: -15vh;
}

.section-stories {
    padding: 15rem 0;
    transform: skewY(-7deg);
    margin-top: -10vh;

    & > * {
        transform: skewY(7deg);
    }
    position: relative;
}

.section-book {
    padding: 15rem;
    background-image: linear-gradient(to right bottom, $primary-color-light, $primary-color-dark);
}

.book {
    // background-image: url('../../assets/nat-10.jpg');
    background-size: cover;
    height: 50rem;
    border-radius: .3rem;
    box-shadow: 0 1.5rem 4rem rgba($color-black, 0.3);
    background-image: linear-gradient(105deg,rgba($color-white, .8) 0%, rgba($color-white, .8) 50%, transparent 50%), url('../../assets/nat-10.jpg');

    &__form {
        width: 50%;
        padding: 6rem;
    }
}