.composition {
    position: relative;

    &__photo {
        width: 55%;
        box-shadow: 0 1.5rem 4rem rgba($color-black, 0.4);
        border-radius: .2rem;
        position: absolute;
        transition: all 0.2s;
        z-index: 1;

        &--p1 {
            left: 0;
            top: -2rem;
        }

        &--p2 {
            right: 0;
            top: 2rem;
        }

        &--p3 {  
            left: 20%;
            top: 10rem;
        }

        &:hover {
            transform: scale(1.05) translateY(-2.5rem);
            box-shadow: 0 2.5rem 4rem rgba($color-black, 0.5);
            z-index: 2;
            outline: 1.5rem solid $primary-color;
            outline-offset: 2rem;

            
        }
    }

    &:hover &__photo:not(:hover){
        transform: scale(0.8);
    }
}