@function calculateWidth($width, $col) { // 
    @if  $width == $col {
     @return 100%;
    }
    @else if $width == 1 {
    @return calc((100% - (#{$col} - #{$width}) * #{$gutter-horizontal}) / (#{$col}));
    }
    @else {
    @return calc(#{$width} * ((100% - (#{$col} - #{$width} ) * #{$gutter-horizontal}) / (#{$col})) + #{$col*1rem - 1rem});
    }
}

// 1 2 1 -> 4
/* 
1st column -> (100% - 3*gutters) / 4
2nd column -> (100% - 2*gutters) / 3 
*/