.row {
    @include clearfix;

    max-width: $grid-width;
    margin: 0 auto;

    &:not(:last-child) {
        margin-bottom: $gutter-vertical;
    }

    [class^=col-] { // could also have been done with %extender inheritance
        float: left;

        &:not(:last-child) {
            margin-right: $gutter-horizontal;
        }
    }

    .col-1-of-2 {
        width: calculateWidth(1,2);
    }

    .col-1-of-3 {
        width: calculateWidth(1,3);
    }

    .col-1-of-4 {
        width: calculateWidth(1,4);
    }

    .col-2-of-4 {
        width: calculateWidth(2,4);
    }

    .col-3-of-4 {
        width: calculateWidth(3,4);
    }

    .col-4-of-4 {
        width: calculateWidth(4,4);
    }

    .col-1-of-5 {
        width: calculateWidth(1,5);
    }

    .col-2-of-5 {
        width: calculateWidth(2,5);
    }

    .col-3-of-5 {
        width: calculateWidth(3,5);
    }

    .col-4-of-5 {
        width: calculateWidth(4,5);
    }
}
