.form {
    &__group:not(:last-child) {
            margin-bottom: 3rem;
        
    }

    &__input {
        font-size: $default-font-size;
        padding: 1.5rem 2rem;
        border-radius: .2rem;
        background-color: rgba($color-white, 0.8);
        border: none;
        font-family: inherit;
        transition: all 0.2s;
        border-bottom: .5rem solid transparent;
        width: 95%;
        color: inherit;

        &::-webkit-input-placeholder {
            color: $color-gray;
        }

        &:focus {
            outline: none;
            box-shadow: 0 1rem 3rem rgba($color-black, 0.1);
            border-bottom: .5rem solid $primary-color;
        }

        &:focus:invalid {
            border-bottom: .5rem solid $color-red ;
        }

       
    }


    &__label {
        font-size: 1.6rem;
        font-weight: 700;
        margin-left: 2rem;
        margin-top: .7rem;
        display: block;
        transform: translate(0);
        
        transition: all 0.2s;
    }

    &__input:placeholder-shown + &__label {
        opacity: 0;
        visibility: hidden;
        transform: translateY(-4rem);
    }

    
}
