@keyframes moveInLeft {
    0% {
        opacity: 0;
        transform: translateX(-10rem);
    }
    80% {
        opacity: 0.8;
        transform: translateX(1rem);
    }
    100% {
        opacity: 1;
        transform: translate(0);
    }
}

@keyframes moveInRight {
    0% {
        opacity: 0;
        transform: translateX(10rem);
    }
    80% {
        opacity: 0.8;
        transform: translateX(-1rem);
    }
    100% {
        opacity: 1;
        transform: translate(0);
    }
}

@keyframes moveUp {
    0% {
        transform: translateY(4rem);
        opacity: 0;
    }

    100% {
        transform: translate(0);
        opacity: 1;
    }
}

@keyframes load-box {
    0% {
        height: 0%;
        width: 0%;
    }

    100% {
        height: 80%;
        width: 60%;
    }
}

@keyframes show {
    0% {
        opacity: 0;
        transform: translateX(-4rem);
    }

    100% {
        transform: translateX(0);
        opacity: 1;
    }
}

@keyframes slide-in {
    0% {
        transform: translateX(-5rem);
        opacity: 0;
    }

    100% {
        transform: translateX(0);
        opacity: 1;
    }
}


@keyframes fadeIn {

    0% {
        opacity: 0;
    }

    100%{
        opacity: 1;
    }
    
}
