.intro {
    position: relative;

    &__bg {
        height: 100vh;
        width: 100vw;
        z-index: 2;
        position: fixed;
        top: 0;
        left: 0;
        background-color: rgba($color-black, 0.8);
        padding: 0;
        backface-visibility: hidden;

        &-container {
            position: relative;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            background-image: linear-gradient(
                105deg,
                rgba($color-gray, 1) 20%,
                rgba($color-gray, 0.8) 35%,
                $color-white 35%
            );
            animation: load-box 1.5s forwards ease-in-out;

            &::after{
                clear: both;
                content: "";
                display: table;
            }

            &--left {
                width: 50%;
                height: 100%;
                overflow: hidden;
                float: left;
                shape-outside: polygon(0 0, 100% 0, 60% 130%, 0 100%);
                position: relative;
                z-index: 2;
                .image-container {
                    height: 30%;
                    width: 40%;
                    clip-path: circle(50% at 50% 50%);
                    margin-left: 8rem;
                    margin-top: 5rem;

                    opacity: 0;
                    animation: show 1s forwards 3s ease-in-out;
                    

                    // outline: 3px solid $color-black;
                    overflow: hidden;

                    .profile {
                        width: 100%;
                        object-fit: cover;
                        background-position: top;
                        transition: transform 0.2s;
                        transform: scale(1);
                    }

                    &:hover .profile {
                        transform: scale(1.2);
                    }
                }

                @media screen and (max-width: 1024px) {
                    .image-container {
                    height: 23%;
                    width: 45%;
                    margin-left: 5rem;
                    }
                }

                
            }

            &--right {
                padding: 3rem;

                &-flow {
                    .paragraph {
                        font-size: 1.2rem;
                    }
                    opacity: 0;
                    animation: fadeIn .5s 4s ease-in forwards;
                }
                
            }
        }
    }
}
